export enum PanelType {
  MultiSwitch = 'Multi Switch',
  Slider = 'Slider',
  Switch = 'Switch',
}

export enum MultiSwitchType {
  BUTTON = 'Button',
  DROPDOWN = 'Drop Down',
}

export interface MultiSwitchTabDataType {
  [id: number]: {
    name: string;
    value: number;
  };
}

export interface MultiSwitchTabType {
  type: MultiSwitchType;
  data: MultiSwitchTabDataType;
}

export interface SimpleOptions {
  panelType: PanelType;
  min?: number;
  max?: number;
  multiSwitchTab?: MultiSwitchTabType;
}

export enum Messaging {
  DEFAULT = 'DEFAULT',
  REQ_RES = 'REQ_RES',
}

export interface TopicProtocol {
  topic: string;
  messaging: Messaging;
}
