import { PanelProps } from './PanelProps';
import { Messaging, MultiSwitchTabType, MultiSwitchType } from '../types';
import React, { useEffect, useRef, useState } from 'react';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grid from '@material-ui/core/Grid';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';

interface MultiSwitchProps extends PanelProps {
  multiSwitchTab?: MultiSwitchTabType;
}

export default function MultiSwitchPanel(props: MultiSwitchProps) {
  const { _client, topicProtocols, setIsRunning, dataValue, multiSwitchTab } = props;

  const classes = useStyles();
  const [originalValue, setOriginalValue] = useState(-1);
  const [originalName, setOriginalName] = useState('');
  const [dropdownOptions, setDropdownOptions] = useState<string[]>([]);
  const [open, setOpen] = useState(false);
  const anchorRef = useRef<HTMLDivElement>(null);
  const [selectedIndex, setSelectedIndex] = useState(1);

  useEffect(() => {
    if (multiSwitchTab && multiSwitchTab.data) {
      Object.keys(multiSwitchTab.data).forEach(key => {
        if (multiSwitchTab.data[+key].value === dataValue) {
          setOriginalName(multiSwitchTab.data[+key].name);
          setSelectedIndex(+key);
        }
      });
      setOriginalValue(dataValue);
      setIsRunning(false);
    }
  }, [dataValue]);

  useEffect(() => {
    if (multiSwitchTab && multiSwitchTab.data) {
      setDropdownOptions(Object.keys(multiSwitchTab.data).map(key => multiSwitchTab.data[+key].name));
    }
  }, [multiSwitchTab]);

  const handleClick = (value: any) => {
    if (!_client.current) {
      return;
    }
    if (!_client.current.connected) {
      _client.current.reconnect();
    }
    setIsRunning(true);
    topicProtocols.forEach(({ topic, messaging }) => {
      if (messaging === Messaging.REQ_RES) {
        topic += '/req';
      }
      _client.current.publish(topic, JSON.stringify({ priority: 16, value }), { retain: true });
    });
    setOriginalValue(value);
  };

  const displayMultiSwitch = () => {
    return (
      multiSwitchTab &&
      Object.keys(multiSwitchTab.data).map(key => {
        const sw = multiSwitchTab.data[+key];
        return (
          <Button color={originalValue === sw.value ? 'default' : 'primary'} onClick={() => handleClick(sw.value)}>
            {sw.name}
          </Button>
        );
      })
    );
  };

  const handleClickDropdown = () => {
    if (multiSwitchTab && multiSwitchTab.data) {
      handleClick(multiSwitchTab.data[selectedIndex].value);
    }
  };

  const handleMenuItemClick = (event: React.MouseEvent<HTMLLIElement, MouseEvent>, index: number) => {
    if (multiSwitchTab && multiSwitchTab.data) {
      setSelectedIndex(index);
      handleClick(multiSwitchTab.data[index].value);
      setOpen(false);
    }
  };

  const handleToggle = () => {
    setOpen(prevOpen => !prevOpen);
  };

  const handleClose = (event: React.MouseEvent<Document, MouseEvent>) => {
    if (!(anchorRef.current && anchorRef.current.contains(event.target as HTMLElement))) {
      setOpen(false);
    }
  };

  const displayDropDown = () => {
    return (
      <Grid container direction="column" alignItems="center">
        <Grid item xs={12}>
          <ButtonGroup variant="contained" color="primary" ref={anchorRef} aria-label="split button">
            <Button onClick={handleClickDropdown}>{originalName}</Button>
            <Button
              color="primary"
              size="small"
              aria-controls={open ? 'split-button-menu' : undefined}
              aria-expanded={open ? 'true' : undefined}
              aria-label="select merge strategy"
              aria-haspopup="menu"
              onClick={handleToggle}
            >
              <ArrowDropDownIcon />
            </Button>
          </ButtonGroup>
          <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
            {({ TransitionProps, placement }) => (
              <Grow
                {...TransitionProps}
                style={{
                  transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom',
                }}
              >
                <Paper>
                  <ClickAwayListener onClickAway={handleClose}>
                    <MenuList id="split-button-menu">
                      {dropdownOptions.map((option, i) => {
                        return (
                          <MenuItem
                            key={option}
                            selected={option === originalName}
                            onClick={event => handleMenuItemClick(event, i)}
                          >
                            {option}
                          </MenuItem>
                        );
                      })}
                    </MenuList>
                  </ClickAwayListener>
                </Paper>
              </Grow>
            )}
          </Popper>
        </Grid>
      </Grid>
    );
  };

  return (
    <div className={classes.root}>
      <ButtonGroup variant="contained" color="primary" aria-label="contained primary button group">
        {multiSwitchTab && multiSwitchTab.type === MultiSwitchType.BUTTON && displayMultiSwitch()}
        {multiSwitchTab && multiSwitchTab.type === MultiSwitchType.DROPDOWN && displayDropDown()}
      </ButtonGroup>
    </div>
  );
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      '& > *': {
        margin: theme.spacing(1),
      },
    },
  })
);
