import React, { useEffect, useState } from 'react';
import Switch, { SwitchClassKey, SwitchProps } from '@material-ui/core/Switch';
import { createStyles, Theme, withStyles } from '@material-ui/core';
import { stylesFactory } from '@grafana/ui';
import { css } from 'emotion';
import { Messaging } from '../types';
import { PanelProps } from './PanelProps';

interface SwitchPanelProps extends PanelProps {}

export default function SwitchPanel(props: SwitchPanelProps) {
  const { _client, topicProtocols, isRunning, setIsRunning, dataValue } = props;
  const [originalValue, setOriginalValue] = useState(false);

  useEffect(() => {
    setOriginalValue(dataValue);
    setIsRunning(false);
  }, [dataValue]);

  const handleClick = () => {
    if (!_client.current) {
      return;
    }
    if (!_client.current.connected) {
      _client.current.reconnect();
    }
    setIsRunning(true);
    topicProtocols.forEach(({ topic, messaging }) => {
      if (messaging === Messaging.REQ_RES) {
        topic += '/req';
      }
      _client.current.publish(topic, JSON.stringify({ priority: 16, value: !originalValue }), { retain: true });
    });
    setOriginalValue(!originalValue);
  };

  const styles = getStyles();

  return (
    <div className={styles.switchWrapper}>
      <IOSSwitch checked={originalValue} disabled={isRunning} onClick={handleClick} />
    </div>
  );
}

const getStyles = stylesFactory(() => {
  return {
    switchWrapper: css`
      display: flex;
      height: 100%;
      align-items: center;
      justify-content: center;
    `,
  };
});

interface Styles extends Partial<Record<SwitchClassKey, string>> {
  focusVisible?: string;
}

interface Props extends SwitchProps {
  classes: Styles;
}

const IOSSwitch = withStyles((theme: Theme) =>
  createStyles({
    root: {
      width: 100,
      height: 52,
      padding: 0,
      margin: theme.spacing(1),
    },
    switchBase: {
      '&.MuiSwitch-colorSecondary.Mui-disabled + .MuiSwitch-track': {
        backgroundColor: '#15171A !important',
      },
      padding: 2,
      '&$checked': {
        transform: 'translateX(48px)',
        color: theme.palette.common.white,
        '& + $track': {
          backgroundColor: '#52d869',
          opacity: 1,
          border: `1px solid ${theme.palette.grey[400]}`,
        },
      },
      '&$focusVisible $thumb': {
        color: '#52d869',
        border: '6px solid #fff',
      },
    },
    thumb: {
      width: 48,
      height: 48,
    },
    track: {
      borderRadius: 50 / 2,
      border: `1px solid ${theme.palette.grey[400]}`,
      backgroundColor: '#7e7e7e',
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  })
)(({ classes, ...props }: Props) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});
