import { Button, stylesFactory } from '@grafana/ui';
import CustomSlider from '../CustomSlider';
import React, { useEffect, useState } from 'react';
import { css } from 'emotion';
import { Messaging } from '../types';
import { PanelProps } from './PanelProps';

interface SliderProps extends PanelProps {
  min: number;
  max: number;
}

export default function SliderPanel(props: SliderProps) {
  const { _client, topicProtocols, min, max, isRunning, setIsRunning, dataValue } = props;
  const [originalValue, setOriginalValue] = useState(0);
  const [currentValue, setCurrentValue] = useState(0);

  useEffect(() => {
    setOriginalValue(dataValue);
    setCurrentValue(dataValue);
    setIsRunning(false);
  }, [dataValue]);

  const onSetValue = () => {
    if (!_client.current) {
      return;
    }
    if (!_client.current.connected) {
      _client.current.reconnect();
    }
    setIsRunning(true);
    setOriginalValue(currentValue);
    topicProtocols.forEach(({ topic, messaging }) => {
      if (messaging === Messaging.REQ_RES) {
        topic += '/req';
      }
      _client.current.publish(topic, JSON.stringify({ priority: 16, value: currentValue }), { retain: true });
    });
  };

  const onResetValue = () => {
    setCurrentValue(originalValue);
  };

  const ThumbComponent = (props: any) => {
    return (
      <span {...props}>
        <span style={{ fontSize: 10 }}>{currentValue}</span>
      </span>
    );
  };

  const styles = getStyles();
  return (
    <div className={styles.slider}>
      <div className={styles.buttonWrapper}>
        <Button onClick={onSetValue} className={styles.buttonStyle}>
          Set
        </Button>
        <Button onClick={onResetValue} className={styles.buttonStyle}>
          Reset
        </Button>
      </div>
      <CustomSlider
        ThumbComponent={ThumbComponent}
        min={min}
        max={max}
        defaultValue={originalValue}
        value={currentValue}
        valueLabelDisplay="on"
        onChange={(e, value) => {
          let val = 0;
          if (typeof value !== 'number') {
            val = value[0];
          } else {
            val = value;
          }
          setCurrentValue(val);
        }}
        disabled={isRunning}
      />
    </div>
  );
}

const getStyles = stylesFactory(() => {
  return {
    buttonWrapper: css`
      margin: 0 auto;
      * {
        margin: 4px;
      }
    `,
    buttonStyle: css`
      height: 44px;
    `,
    slider: css`
      flex-direction: column;
      padding: 10px;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    `,
  };
});
